  /* display: flex; */
  /* padding: 0; */
  /* background-color: white; */
  /* border: 1px solid black; */
  /* border-radius: 8px 0 0 8px; */
}
.home-container .container h1 {
  font-size: 40px;
  color: #0e6e02;
  font-weight: 600;
  animation: fadein 3s;
  /* margin-bottom: 20px; */
}
.home-container .container .text {
  /* width: 90%; */
  /* padding: 30px 0; */
  text-align: center;
  /* margin: 0 auto; */
}
.home-container form {
  /* padding: 20px 0; */
  position: relative;
  z-index: 2;
  width: 100%;
  margin: auto;
}
.home-container form input {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  outline: 0;
  border: 1px solid #0e6e02;
  /* width: 100%; */
  border-radius: 3px;
  /* padding: 10px 15px; */
  /* margin: 10px auto 10px auto; */
  display: block;
  text-align: center;
  font-size: 18px;
  color: var(--blue);
  transition-duration: 0.25s;
}
.home-container form input:hover {
  background-color: var(--blue);
  color: #fbd3e9;
}
.home-container form input:focus {
  background-color: #0e6e02;
  /* width: 300px; */
  color: #fbd3e9;
}
.home-container form ::placeholder {
  color: #0e6e02;
}
.home-container form input:hover::placeholder {
  color: white;
}
.home-container form button {
  color: var(--red);
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  outline: 0;
  background-color: var(--blue);
  border: 0;
  padding: 10px 15px;
  color: #fbd3e9;
  border-radius: 3px;
  font-size: 20px;
  /* width: 60%; */
  cursor: pointer;
  transition-duration: 0.25s;
}
.home-container form button:hover {
  border: 1px solid #0e6e02;
  border-radius: 5px;
  background-color: #f5f7f9;
  color: var(--blue);
}
