/* Media queries ~ */
@media screen and (max-width: 1000px) {
  .home-container .container {
    /* max-width: 100vw; */
  }
}
@media screen and (max-width: 678px) {
  .login-container {
    padding-top: 10%;
  }
  .home-container .container {
    max-width: 100vw;
  }
  .home-container form {
    width: 100%;
  }
  .home-container .loginDesign {
    display: none;
  }
  .home-container .container .text {
    width: 100%;
    /* padding: 80px 20px; */
  }
}

